<script>
import { saveSettings }  from '../store/actions'
import { getWorkspaceData, getSettings, getElements } from '../store/getters'
import modal from './modal.vue'
import { merge } from 'lodash'

export default {
  components: {
    modal
  },
  vuex: {
    actions: {
      saveSettings
    },
    getters: {
      workspace: getWorkspaceData,
      pageSettings: getSettings,
      elements: getElements
    }
  },
  props: {
    show: {
      type: Boolean,
      required: true,
      twoWay: true    
    }
  },
  data (){
    return {
      currentTab: 'seo',
      changed: false,
      settings: merge({}, this.pageSettings),
      formsInPage: false,
      goals: []
    }
  },
  methods:{
    save: function(){
      if (this.changed) {
        this.saveSettings(this.settings);
        this.changed = false;
      }
      this.show = false;
    }
  },
  watch:{
    'settings': {
      deep:true,
      handler: function(val){
        this.changed = true;
      }
    }
  },
  ready: function(){
    // let vm = this;
    for (let elementId in this.elements){
      if (this.elements[elementId].type === "form"){
        this.formsInPage = true;
      }
      if (this.elements[elementId].link && this.elements[elementId].link.url){
        this.goals.push(this.elements[elementId].link.url);
      }
    }
    if (this.formsInPage){
      this.goals.push('form');
    }
  }
}
</script>

<template>
  <modal :show.sync="show" :width="'800px'" :height="'500px'" >
    <div slot="header">
      <ul class="nav nav-pills">
        <li role="presentation" :class="{active: currentTab === 'seo'}"><a href="javascript:;" @click="currentTab = 'seo'"></a></li>
        <li role="presentation" :class="{active: currentTab === 'goal'}"><a href="javascript:;" @click="currentTab = 'goal'"></a></li>
        <!-- <li role="presentation" :class="{active: currentTab === 'color'}"><a href="javascript:;" @click="currentTab = 'color'"></a></li> -->
        <li role="presentation" :class="{active: currentTab === 'code'}"><a href="javascript:;" @click="currentTab = 'code'">Javascript</a></li>
        <!-- <li role="presentation" :class="{active: currentTab === 'background'}"><a href="#" @click="currentTab = 'background'"></a></li> -->
        <!-- <li role="presentation" :class="{active: currentTab === 'font'}"><a href="#" @click="currentTab = 'font'"></a></li> -->
        <!-- <li role="presentation" :class="{active: currentTab === 'data'}"><a href="#" @click="currentTab = 'data'"></a></li> -->
        <!-- <li role="presentation" :class="{active: currentTab === 'editor'}"><a href="#" @click="currentTab = 'editor'"></a></li> -->
      </ul>
    </div>
    
    <div slot="body" class="settings-body">
      <div v-show="currentTab === 'seo'">
        <form class="form-horizontal">
          <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="settings.seo.pageTitle" placeholder="">
            </div>
          </div>
          <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <input type="text" class="form-control" v-model="settings.seo.keywords" placeholder="">
            </div>
          </div>
          <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <textarea class="form-control" rows="3"  v-model="settings.seo.description"></textarea>
            </div>
          </div>
          
          <!-- <div class="form-group">
            <div class="col-sm-offset-2 col-sm-10">
              <div class="checkbox">
                <label>
                  <input type="checkbox"> 
                </label>
              </div>
            </div>
          </div> -->
        </form>
      </div>
      <div v-show="currentTab === 'goal'">
        <form class="form-horizontal">
          <div class="form-group">
            <label class="col-sm-2 control-label"></label>
            <div class="col-sm-10">
              <select class="form-control" v-model="settings.goals.first">
                <option value=""></option>
                <option v-for="goal in goals" :value="goal">{{(goal === 'form') ? '' : '[] ' + goal}}</option>
              </select>
            </div>
          </div>
        </form>
      </div>
      <div v-show="currentTab === 'code'">
        <form class="form-horizontal">
          <div class="form-group">
            <label class="col-sm-2 control-label">&lt;header&gt;</label>
            <div class="col-sm-10">
              <textarea class="form-control" rows="4"  v-model="settings.code.header"></textarea>
            </div>
          </div>
          <div class="form-group">
            <label class="col-sm-2 control-label">&lt;body&gt;</label>
            <div class="col-sm-10">
              <textarea class="form-control" rows="4"  v-model="settings.code.bodyIn"></textarea>
            </div>
          </div>
          <div class="form-group">
            <label class="col-sm-2 control-label">&lt;body&gt;</label>
            <div class="col-sm-10">
              <textarea class="form-control" rows="4"  v-model="settings.code.bodyOut"></textarea>
            </div>
          </div>
        </form>
      </div>
      <div v-show="currentTab === 'data'">
        <form class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-offset-2 col-sm-10">
              <div class="checkbox">
                <label>
                  <input type="checkbox"> 
                </label>
              </div>
            </div>
          </div>
          <hr style="border-bottom:1px doshed #ccc">
          <h4>API</h4>
          <div class="form-group">
            <label for="settings-ga-id" class="col-sm-2 control-label">GAID</label>
            <div class="col-sm-10">
              <input type="text" class="form-control" id="settings-ga-id" placeholder="UA-88888888-8">
            </div>
          </div>
        </form>
      </div>

    </div>
    <div slot="footer">
      <button class="btn btn-success btn-sm" @click="save"></button>
    </div>
  </modal>
</template>

<style>

.settings-body {
  width:95%;
}

</style>



