export function getWorkspaceData (state) {
  return state.workspace
}

export function getSections (state) {
  return state.page.sections
}

export function getElements (state) {
  return state.page.elements
}

export function getPage (state) {
  return state.page
}

export function getSettings (state) {
  return state.page.settings
}

export function getColorSet (state) {
  return state.page.colorSet
}



